body {
  margin: 0;
  padding: 0;
  font-family: 'Montserrat';
  overflow: hidden;
  height: 100%;
}

label {
  font-size:20px;
  width: 400px;
}

button {
  cursor: pointer;
  font-size: 1rem;
  padding: 0.6em 1em;
  border-radius: 3px;
  box-shadow: 2px 4px 10px rgba(0, 0, 0, 0.3);
  transition: all 0.3s ease;
}

.navbar {
  background: #0097a7;
  height: 4em;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.3);
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.navbar .nav .links a,
.navbar span {
  margin: 0 1em;
  color: #f8f8f8;
  text-decoration: none;
  font-size: 1.1rem;
  transition: all 0.4s ease;
}

.navbar .nav .links a:hover {
  color: #ffccbc;
}

.navbar .nav {
  display: flex;
  transition: all 0.4s ease;
}

.container {
  height: 100%;
}

.row {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 3em;
}
.form-container-sign-up {
  align-self: center;
  /* padding: 1em; */
  width: 80%;
  height: 80%;
  /* margin: 4em auto; */
  margin-top: 20px;
  margin-bottom: 200px;
  border: 2px solid #b0bec5;
  border-radius: 6px;
  box-shadow: 2px 4px 10px rgba(0, 0, 0, 0.3);
  transition: all 2s ease;

}

.form-container-sign-up .danger {
  background: #e57373;
}

.form-container-sign-up h3 {
  text-align: center;
}

.form-container-sign-up form {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin: auto;
  width: 80%;
}

.form-container-sign-up form input {
  width: 100%;
  padding: 0.6em 1em;
  margin: 0.5em auto;
  font-size: 1rem;
  border-radius: 3px;
  border: 2px solid #e0e0e0;
  outline: none;
  transition: all 0.4s ease;
}

.form-container-sign-up form input:focus,
.form-container-sign-up form input:active {
  border: 2px solid #82ada9;
}

.form-container-sign-up form input:valid {
  border: 2px solid #66bb6a;
}

.form-container-sign-up form button {
  width: 50%;
  margin: 1em auto;
  background: #000033;
  border: none;
  color:white;
}

.form-container-sign-up form button:hover {
  box-shadow: 3px 8px 16px rgba(0, 0, 0, 0.5);
}

.form-container-sign-up form a {
  margin: auto;
}

.form-container-sign-up form label {
  margin-top: 0.6em;
}

.layout {
  display: flex;
  flex-direction: column;
  overflow-y: scroll;
  height: Calc(100vh - 4em);
}

.layout .content {
  display: flex;
  flex-direction: row;
  position: relative;
  flex: 1 1;
}

.layout .content nav {
  display: flex;
  flex-direction: column;
  align-items: center;
  flex: 0.8 1;
  z-index: 1000;
  background: #f8f8f8;
  box-shadow: inset -10px 8px 10px -6px rgba(0, 0, 0, 0.3);
}

.layout .content nav a {
